body {
  width: 100%;
  font-family: "Open Sans", sans-serif;
  padding: 0;
}

body.__overflow {
  overflow: hidden;
}

a, a:hover, a:focus {
  color: inherit;
  text-decoration: none;
}

ul {
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;
}

/**** home main ****/
.home .main {
  background: transparent;
  position: fixed;
  left: 0;
  right: 0;
  transition: 0.3s ease-in-out;
  box-shadow: none;
}

.home .main.__bg-color {
  box-shadow: 0px 1px 5px rgba(100, 100, 100, 0.3);
  background: #fff;
  transition: 0.3s ease-in-out;
}

.home .main #logo {
  fill: #000;
}

.home .main.__bg-color #logo {
  fill: #3f4c94;
}

/**** btn-round ****/
.btn-round-xs {
  width: 30px;
  height: 30px;
}

.btn-round-sm {
  width: 50px;
  height: 50px;
}

.btn-round {
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

/**** btn ****/
.btn-default {
  border-radius: 1.5rem;
}

.btn-default-primary {
  background: #64C9FE;
  border: 2px solid #64C9FE;
  color: #fff;
  transition: 0.3s ease-in-out;
}

.btn-default-primary:hover, .btn-default-primary:focus {
  color: #64C9FE;
  background: transparent;
  box-shadow: none;
}

.btn-outline {
  border: 2px solid transparent;
  border-radius: 1.5rem;
}

.btn-outline-primary {
  color: #64C9FE;
  background: transparent;
  background-image: none;
  border-color: #64C9FE;
}

.btn-outline-primary:hover, .btn-outline-primary:focus {
  color: #fff;
  background: #64C9FE;
  border-color: #64C9FE;
}

.btn-outline-white {
  color: #64C9FE;
  background: #fff;
  background-image: none;
  border-color: #fff;
}

.btn-outline-white:hover, .btn-outline-white:focus {
  color: #fff;
  background: transparent;
  border-color: #fff;
}

.btn-default-lg {
  font-size: 1.25rem;
  padding: 0.5rem 2rem;
  border-radius: 3rem;
}

.btn .svg-inline--fa {
  margin-left: 10px;
}

.btn-dark:hover, .btn-dark:focus {
  color: #fff;
}

.btn-dark:focus {
  box-shadow: none;
}

.btn-dark:not(:disabled):not(.disabled):active:focus {
  box-shadow: none;
}

.btn-outline-primary:not(:disabled):not(.disabled):active, .btn-outline-primary:not(:disabled):not(.disabled).active, .show > .btn-outline-primary.dropdown-toggle {
  background: #64C9FE;
  border-color: #64C9FE;
}

.btn-outline-primary:focus, .btn-outline-primary.focus {
  box-shadow: none;
}

.btn-outline-primary:not(:disabled):not(.disabled):active:focus, .btn-outline-primary:not(:disabled):not(.disabled).active:focus, .show > .btn-outline-primary.dropdown-toggle:focus {
  box-shadow: none;
}

/**** title section ****/
.title-section {
