}

/**** icon hamb ****/
.nav-btn {
  z-index: 5000;
  position: relative;
  transition: transform 0.3s;
  width: 65px;
  height: 80px;
  float: right;
  cursor: pointer;
}

.nav-btn .nav-fill {
  display: block;
  position: absolute;
  top: 50%;
  right: 0;
  width: 30px;
  height: 2px;
  background: #000;
}

.nav-btn .nav-fill::before, .nav-btn .nav-fill::after {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 30px;
  height: 2px;
  background: #000;
  transform: translateY(-8px);
  transition: transform 0.3s;
}

.nav-btn .nav-fill::before {
  width: 30px;
  left: 0;
}

.nav-btn .nav-fill::after {
  width: 20px;
  left: 0;
  transform: translateY(8px);
}

.nav-btn:hover .nav-fill::before {
  transform: translateY(-10px);
}

.nav-btn:hover .nav-fill::after {
  transform: translateY(10px);
}

.nav-btn.__is-open .nav-fill {
  background: transparent !important;
}

.nav-btn.__is-open .nav-fill::before {
  width: 30px;
  left: -5px;
  background: #fff;
  transform: translateY(0) rotate(45deg);
  transition: 0.3s, transform 0.3s;
}

.nav-btn.__is-open .nav-fill::after {
  width: 30px;
  left: -5px;
  transform: translateY(0) rotate(-45deg);
  transition: 0.3s, transform 0.3s;
  background: #fff;
}

.nav-txt {
  color: #000;
  transform: rotate(-90deg) translateX(20%);
  position: absolute;
  left: 5px;
  top: 50%;
  font-size: 12px;
}

.nav-txt.__nav-txt-white {
  color: #fff;
}

/**** menu-item ****/
.wrap-nav {
  color: #fff;
  z-index: 100;
  position: absolute;
  width: 55%;
  height: 100vh;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 130px 90px 50px 90px;
  overflow: hidden;
  opacity: 0;
  transform: translateX(-100%);
  transition: transform 0.3s, opacity 0.3s;
  transition-timing-function: ease-in;
}

.wrap-nav.__is-open {
  opacity: 1;
  transform: translateX(0);
  transition-timing-function: cubic-bezier(0.01, 1.07, 0.54, 1.01);
  transition-delay: 0.55s;
}

.wrap-nav .menu {
  font-size: 30px;
  font-family: "Montserrat", sans-serif;
  text-transform: uppercase;
  font-weight: 900;
}

.wrap-nav .menu .menu-item {
  position: relative;
  overflow: hidden;
  margin: 10px 0;
}

.wrap-nav .menu .menu-item.current-menu-item {
  color: #64C9FE;
}

/**** menu-item hover effect ****/
.menu-item .__color-nav {
  transition: 0.5s ease-in-out;
}

.menu-item .__color-nav:hover {
  padding-left: 15px;
  color: #64C9FE;
  transition: 0.5s ease-in-out;
}

.menu-item .__color-nav::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 30%;
  background: #64C9FE;
  transform: translate3d(-150%, 0, 0);
  transition: transform 0.7s, width 0.12s;
  transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
  z-index: -1;
}

.menu-item .__color-nav:hover::before {
  transform: translate3d(115%, 0, 0);
  left: 0;
  width: 100%;
}

/**** main-open ****/
.shape-overlays {
  width: 100vw;
  height: 100vh;
  z-index: 10;
