  transform: translateY(0);
  transition: transform 0.3s;
  transition-delay: 0.85s;
}

.txt-job span:nth-child(2) {
  transform: translateY(0);
  transition: transform 0.3s;
  transition-delay: 0.85s;
}

.txt-job span:nth-child(3) {
  transform: translateY(0);
  transition: transform 0.3s;
  transition-delay: 0.85s;
}

.txt-job span:nth-child(4) {
  transform: translateY(0);
  transition: transform 0.3s;
  transition-delay: 0.85s;
}

.txt-job span:nth-child(6) {
