/***** effect thumbnail *****/
/*---------------*/
.effect-thumb {
  height: 100%;
  background-size: cover;
  background-position: center;
  transition: opacity 0.35s, transform 0.35s;
  width: 100%;
  backface-visibility: hidden;
  overflow: hidden;
}

.effect-thumb h4 {
  margin-bottom: 1rem;
  text-transform: uppercase;
  font-weight: 300;
  font-size: 2.2rem;
  opacity: 0;
  transition: opacity 0.35s, transform 0.35s;
  transform: translate3d(0, 20px, 0);
  border-left: 3px solid #fff;
  padding-left: 10px;
}

.effect-thumb:hover {
  transform: translate3d(0, 0, 0);
}

.effect-thumb:hover h4 {
  opacity: 1;
  transform: translate3d(0, 0, 0);
}

.thumb-cover::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #64C9FE;
  content: '';
  opacity: 0.5;
  transform: translate3d(0, 100%, 0);
  transition: opacity 0.35s, transform 0.35s;
}

.effect-thumb:hover .thumb-cover::before {
  opacity: 1;
  transform: translate3d(0, 0, 0);
  transition: opacity 0.35s, transform 0.35s;
}

.effect-content {
  position: absolute;
  width: calc(100% - 30px);
  top: 0;
  color: #fff;
  padding: 25px 15px 15px;
}

.effect-content span {
  display: inline;
  padding-left: 5px;
  padding-right: 25px;
  font-size: 1.6rem;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  background: #64C9FE;
}

